/* 스킵 메뉴 */
#skip {
    position: relative;
    z-index: 100000;
}
#skip a {
    background-color: #000;
    color: #fff;
    position: absolute;
    padding: 20px 40px;
    left: -400px;
    top: 5px;
}
#skip a:active,
#skip a:focus {
    left: 5px;
}
/* fixed */
.fixed {
    height: 100vh;
    overflow: hidden;
}
body {
    overflow: overlay;
}
/* 컨테이너 */
.container {
    width: 100%;
    max-width: 1160px;
    padding: 0 20px;
    margin: 0 auto;
    box-sizing: border-box;
}
@media(max-width:1200px){

}
/* 섹션 공통요소 */
.section {
    padding: 120px 0;
}

.section>h2 {
    /* + 는 형제, > 는 자식 */
    font-size: 50px;
    line-height: 1;
    text-align: center;
    margin-bottom: 20px;
}

.section>p {
    font-size: 22px;
    font-weight: 300;
    color: #666;
    text-align: center;
    margin-bottom: 70px;
    padding: 0 20px;
    word-break: keep-all;
    line-height: 1.5;
}

.section > span {
    display: block;
    text-align: center;
    font-size: 16px;
    text-decoration: underline;
    margin-bottom: 20px;
}

@media(max-width: 960px){
    .section {
        padding: 100px 0;
    }
    .section>h2 {
        font-size: 40px;
    }
    .section>p {
        font-size: 18px;
        margin-bottom: 60px;
    }
}
@media(max-width: 600px){
    .section {
        padding: 80px 0;
    }
    .section>h2 {
        font-size: 30px;
    }
    .section>p {
        font-size: 16px;
        margin-bottom: 50px;
    }
}
